/* Обнуляем отступы и задаём высоту для корректного центрирования */
body, html, #root {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #f4b9cf;
}

/* Основной контейнер со светлым фоном */
.app {
  display: flex;
  flex-direction: column;
  /*min-height: 60vh;*/
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}

/* Область для отображения романтической фразы */
.phrase {
  font-size: 20px;
  font-weight: bold;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.heart-container {
  position: relative;
  width: calc((50px + 2px) * 13 - 2px);
  /*height: calc((50px + 2px) * 13 - 2px);*/
  transform-origin: center;
}

/* Стили для пикселя: размер, граница и фон в розовой гамме */
.pixel {
  width: 50px;
  height: 50px;
  border: 1px solid #ff69b4;
  background-color: #ffc0cb;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
}

.pixel:hover {
  transform: scale(1.1);
}

/* Анимация плавного изменения цвета текста — длительность 4s.
   Задержка (animation-delay) для каждого пикселя задаётся индивидуально. */
.pixel-text {
  animation: colorChange 4s linear infinite alternate;
  font-size: 20px;
}

@keyframes colorChange {
  0% {
    color: #ff5555;
  }
  50% {
    color: #ff99aa;
  }
  100% {
    color: #ff5555;
  }
}

/* Адаптивное масштабирование для мобильных устройств */
@media (max-width: 768px) {
  .heart-container {
    transform: scale(0.8);
  }
  .phrase {
    padding: 50px 10px 0 0;
  }
}

@media (max-width: 520px) {
  .heart-container {
    transform: scale(0.6);
  }
}

@media (max-width: 480px) {
  .heart-container {
    transform: scale(0.5);
  }
}
